.page {
  &__body {
    margin: 0;
    background-color: $backgroundColor;
  
    &--with-menu {
      overflow: hidden;
    }
  }
  
  &__header {
    margin: 0 auto;
  }
  
  &__menu {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform $animationTime;
  
    &:target {
      transform: translateX(0);
    }
  }
}
