.menu {
  box-sizing: border-box;
  height: 100vh;
  padding: 100px 0;
  background-color: $backgroundColor;

  overflow-y: auto;
  
  &__icon {
    width: 18px;
    height: 12px;
    background-image: url(../images/menu.svg);
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    top: 33px;
    left: 23px;
  
    @include onTablet {
      left: 10%;
    }
  
    @include onDesktop {
      left: 8%;
      top: 13%;
    }
  }
  
  &__top {
    margin-bottom: 48px;
  }
  
  &__list {
    margin: 0 0 48px;
    padding: 0;
    list-style: none;
  }
  
  &__item {
    margin-bottom: 24px;
  
    &:last-child {
      margin-bottom: 0;
    }
  }
  
  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    font-weight: bold;
    font-size: 22px;
    line-height: 22px;
    color: $colorBlack;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: border-color $animationTime;
  
    &:hover {
      border-color: $colorBlack;
    }
  }
  
  &__call {
    display: block;
    width: max-content;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $colorBlack;
    text-decoration: none;
  
    &--phone {
      margin-bottom: 16px;
    }
  
    &--action {
      padding-bottom: 8px;
      border-bottom: 1px solid $colorBlack;
    }
  }
}
